import React from 'react';
import SearchBar from './RecipeSearchBar';
import Recipelist from './Recipelist';
import edamam from '../api/edamam';
import './Recipes.css';

const APP_ID="e1d3bf1f";
const APP_KEY="e263337a85397e2a22a35fc092d68586";

class App extends React.Component{
    state={dishes:[]};
    componentDidMount(){
        this.onTermSubmit('banana');
    }
  
    onTermSubmit = async (term)=>{
        const response = await edamam.get('/search',{
        params:{
            app_id:APP_ID,
            app_key:APP_KEY,
            q:term
        }
    })
    this.setState({dishes:response.data.hits});
    }
    render(){
        return (
        <div className="ui container">
        <div className="ui container">
            <SearchBar onFormSubmit = {this.onTermSubmit}/>
            </div>
            <div className="style">
            {this.state.dishes.map((dish)=>(
                <Recipelist
                title={dish.recipe.label}
                calories={dish.recipe.calories}
                source={dish.recipe.image}
                key={dish.recipe.label}
                ingredients={dish.recipe.ingredients}
                />
            ))}
            </div>
        </div>
        );
    };
    };

export default App;